<template>
  <div class="ajaxUtil">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="codeArea">

<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;button onclick="loadData()"&gt;执行请求&lt;/button&gt;
&lt;script type="text/javascript"&gt;
    import ajaxUtil from "cy_static/ajaxUtil";
    import ui from "cy_static/ui";
    function loadData() {
        ajaxUtil.loadData("xxxx/xxx.do", {
            method : "POST",
            standard : true,
            headers : {
              "Content-Type" : "application/json"
            },
            beforeSend: function (xhr, request) {
                ui.dialog.alert(JSON.stringify(request));
            }
        }).then(function(data) {
            ui.dialog.alert(JSON.stringify(data));
        }).catch(function(data) {
            ui.toast("error");
        });        
    }
&lt;/script&gt;
</xmp >

        </div>
        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/runcode"></iframe>
        </div>
    </div>
    <div class="cy_center">
      <h2 class="cy_h2">ajaxUtil 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
	import ajaxUtil from "cy_static/ajaxUtil"; 
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;script type="text/javascript"&gt;
    ajaxUtil.loadData("xxxx/xxx.do", {
        method : "POST",
        standard : true,
        headers : {
           "Content-Type" : "application/json"
        }
    }).then(function(data) {
        // code
    });
&lt;/script&gt;
</xmp >

      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">对象方法</h2>				
            <p><a>loadData</a>(url : String, request : Object = {}) : <a>Promise</a></p>
						<p>ajax帮助类。方法通过 HTTP 请求加载远程数据；<b>请求数据经过MD5加密：</b>用法与ajax相同</p>
            <table class="table-props">
              <tr>
										<th colspan="3">request 参数</th>
									</tr>
									<tr>
										<th>参数名</th>
										<th>类型</th>
										<th>描述</th>
									</tr>
									<tr class="atn">
										<td>standard</td>
										<td class="atn">Boolean</td>
										<td>是否使用标准格式传送数据(即把数据放在data里包起来)；默认：false</td>
									</tr>
									<tr>
										<td>method</td>
										<td class="atn">String</td>
										<td>请求方式 ("POST" 或 "GET")， 默认为 "GET"；（其他方式Content-Type以application/json值传后端）</td>
									</tr>
									<tr>
										<td>body</td>
										<td class="atn">Object</td>
										<td>发送到服务器的数据，必须为 Key/Value 格式，GET请求中将附加在 URL后；Post表单流动形式，其他以字符串形式（JSON.stringify）；</td>
									</tr>
									<tr>
										<td>dataType</td>
										<td class="atn">String</td>
										<td>预期服务器返回的数据类型：默认："json"<br>
											"json": 返回 JSON数据；<br>
											"text": 返回纯文本字符串；<br>
											"script": 返回纯文本 JavaScript代码；<br>
											"xml": 返回 XML文档；<br>
										</td>
									</tr>
									<tr>
										<td>Content-Type</td>
										<td class="atn">String</td>
										<td>
										发送信息至服务器时内容编码类型：<br>
										"get方式": ""；<br>
										"post方式": "application/x-www-form-urlencoded"；<br>
										"默认": "application/json"；<br>
										</td>
									</tr>
									<tr>
										<td>headers</td>
										<td class="atn">Object</td>
										<td>设置请求头信息；必须为 Key/Value 格式</td>
									</tr>
									<tr>
										<td>timeout</td>
										<td class="atn">Number</td>
										<td>设置请求超时时间（毫秒）</td>
									</tr>
									<tr>
										<td>async</td>
										<td class="atn">Boolean</td>
										<td>默认值: true。默认设置下，所有请求均为异步请求。</td>
									</tr>
									<tr>
										<td>username</td>
										<td class="atn">String</td>
										<td>用于响应 HTTP 访问认证请求的用户名</td>
									</tr>
									<tr>
										<td>password</td>
										<td class="atn">String</td>
										<td>用于响应 HTTP 访问认证请求的密码</td>
									</tr>
									<tr>
										<td>beforeSend</td>
										<td class="atn">Function</td>
										<td>在发送请求之前调用，并且传入一个 XMLHttpRequest 作为参数；返回false将取消AJAX</td>
									</tr>
									<tr>
										<td>error</td>
										<td class="atn">Function</td>
										<td>在请求出错时调用。传入 Error；</td>
									</tr>
									<tr>
										<td>success</td>
										<td class="atn">Function</td>
										<td>当请求成功之后调用。传入返回后的数据；</td>
									</tr>
                  <tr>
                    <td colspan="5">
<codemirror ref="demoCode1" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    ajaxUtil.loadData("xxxx/xxx.do", {
        method : "POST",
        standard : true,
        body : {name : "张三"},
        error : function(data) {}
    }).then(function(data) {
       // code
    });
    //
    ajaxUtil.loadData("xxxx/xxx.do", {
        method : "POST",
        body : {name : "张三"},
        success : function(data) {
           // code
        }
    });
&lt;/script&gt;
</xmp >
                  </td>
								</tr>
            </table> 
      </div>




    </div> 
  </div>
</template>

<script>
import {trim} from "../../../assets/js/codeStyle";
export default {
  name: 'ajaxUtil',
  data () {
    return {
      msg: 'ajaxUtil',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      },
      cmOptions2:{
            readOnly:"true",
            theme : "mdn-like",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  }, 
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
  }, 
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>    
<style scoped>
  .cy_example .codeArea{height:400px}
  .cy_example .iframeBox{flex:10}
</style>


